import { Component, OnInit } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-delete',
  templateUrl: './delete.component.html',
  styleUrls: ['./delete.component.css']
})
export class DeleteComponent implements OnInit {
  deleteName: string
  textToShow: string 
  showSuccess: boolean = false 
  wait: boolean = false

  show(){
    this.showSuccess = true
    setTimeout( ()=> {
      this.showSuccess = false
      this.textToShow = undefined
    }
    , 5000)
  }


  constructor(private http: HttpClient){}

  delete(){
    this.wait = true
    if (this.deleteName)
      this.http.delete('https://immense-fortress-17915.herokuapp.com/?name='+this.deleteName).subscribe((response)=> 
      {
        this.textToShow = "request sended"
        this.show()
        this.wait = false
      }, (err)=>{
        this.textToShow = "error"
        this.show()
        this.wait = false
      })
  else {
    this.textToShow = "wrong input"
    this.show()
    this.wait = false
  }
  }

  ngOnInit(): void {
  }

}
