import { Component, OnInit } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-find',
  templateUrl: './find.component.html',
  styleUrls: ['./find.component.css']
})
export class FindComponent implements OnInit {
  response: any;
  tableMode: boolean = false
  textToShow: string 
  showSuccess: boolean = false 
  wait: boolean = false

  show(){
    this.showSuccess = true
    setTimeout( ()=> {
      this.showSuccess = false
      this.textToShow = undefined
    }
    , 5000)
  }

  buttonName(name: string){
    if (name==='search') return this.response? 'reload table'  : 'search'
    return this.tableMode? 'close table'  : 'open table'
  }

  changeMode(){
    this.tableMode = !this.tableMode
  }

  constructor(private http: HttpClient){}
 
  search(){
    this.wait = true
    this.http.get('https://immense-fortress-17915.herokuapp.com').subscribe((response) => {
      this.response = response
      this.textToShow = "table is ready"
      this.show()
      this.wait = false
    }, (err)=>{
      this.textToShow = "error"
      this.show()
      this.wait = false
    })
  }

  ngOnInit(): void {
  }

}
