import { Component, OnInit } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  cheakName: string
  cheakPassword: string
  textToShow: string 
  success: boolean = false
  showSuccess: boolean = false 
  wait: boolean = false


  constructor(private http: HttpClient){}

  show(){
    this.showSuccess = true
    this.showText()
    setTimeout( ()=> {
      this.showSuccess = false
      this.textToShow = undefined
    }
    , 5000)
  }

  showText(){
    if (!this.textToShow){
      if (this.success) this.textToShow = "login success"
      else this.textToShow = "user does not exist"
    }
  }

  cheak(){
    this.wait = true
    if (this.cheakName && this.cheakPassword)
      this.http.post('https://immense-fortress-17915.herokuapp.com/login', {name: this.cheakName, password: this.cheakPassword}).subscribe((response: any) => {
        this.success = response.status
        this.showText()
        this.show()
        this.wait= false
      }, (err) => {
        console.log(err)
        this.textToShow = "error"
        this.success = false
        this.show()
        this.wait= false
      })
      else {

        this.textToShow = "wrong input"
        this.success = false
        this.show()
        this.wait= false
      }


  }

  ngOnInit(): void {
  }

}
